import React, { Component } from 'react';
import { Row, Col, Form, Tabs, Select, InputNumber, Checkbox, Button } from 'antd';
import FilterPathEducate from 'components/common/input/FilterPathEducate';
import { schoolYearsTime } from 'dump/data';
import TableQualifiedToGraduate from './TableQualifiedToGraduate';
import TableNotQualifiedToGraduate from './TableNotQualifiedToGraduate';
import TableAwardGraduateDiploma from './TableAwardGraduateDiploma';
import './style.scss';

@Form.create({ name: 'formSerchIndividual' })
class FormInfoStudentForGraduation extends Component {
  state = {};
  handleReset = () => {
    this.props.form.resetFields();
  };
  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      console.log('Received values of form: ', values);
    });
  };
  render() {
    const { getFieldDecorator } = this.props.form;
    const { Option } = Select;
    const { TabPane } = Tabs;
    return (
      <Form onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <FilterPathEducate form={this.props.form} />
          <Col span={8}>
            <Form.Item label="Nm hc:">
              {getFieldDecorator('schoolYearsTime', {})(
                <Select>
                  {schoolYearsTime.map(item => (
                    <Option value={item.code}>{item.name}</Option>
                  ))}
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="t xt:">
              <InputNumber min={1} max={3} defaultValue={1} style={{ width: '100%' }} />
            </Form.Item>
          </Col>
          <Col span={4}>
            <Form.Item label=" ">
              <Checkbox> In lun ra my in</Checkbox>
            </Form.Item>
          </Col>
          <Col span={2}>
            <Form.Item label=" ">
              <Button type="primary">Xt tt nghip</Button>
            </Form.Item>
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <Tabs defaultActiveKey="1">
              <TabPane tab=" iu kin tt nghip ra trng" key="1">
                <TableQualifiedToGraduate />
              </TabPane>
              <TabPane tab="Cha  iu kin tt nghip" key="2">
                <TableNotQualifiedToGraduate />
              </TabPane>
              <TabPane tab="Cp pht vn bng tt nghip" key="3">
                <TableAwardGraduateDiploma />
              </TabPane>
            </Tabs>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default FormInfoStudentForGraduation;



