import React, { Component } from 'react';
import { Row, Col, Form, Input, DatePicker, Button, Icon, Progress } from 'antd';
import moment from 'moment';
import './style.scss';

@Form.create({ name: 'formSerchIndividual' })
class TopBarInputForTabAwardGraduate extends Component {
  state = {};
  handleReset = () => {
    this.props.form.resetFields();
  };
  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      console.log('Received values of form: ', values);
    });
  };
  render() {
    const { getFieldDecorator } = this.props.form;
    const dateFormatList = ['DD/MM/YYYY', 'DD/MM/YY'];
    return (
      <Form onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <Col span={6}>
            <Form.Item label="S Q tt nghip:">
              {getFieldDecorator('decisionNumber', {})(<Input />)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Ngy Q tt nghip:">
              {getFieldDecorator('decisionDate', {})(
                <DatePicker
                  defaultValue={moment('01/01/2015', dateFormatList[0])}
                  format={dateFormatList}
                />
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="S bng t s - n s:">
              {getFieldDecorator('rangeDecisionNumber', {})(
                <Row gutter={24}>
                  <Col span={12}>
                    <Input placeholder="T s" />
                  </Col>
                  <Col span={12}>
                    <Input placeholder="n s" />
                  </Col>
                </Row>
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="ui s bng:">
              {getFieldDecorator('tailDecisionNumber', {})(
                <div>
                  <Input />
                </div>
              )}
            </Form.Item>
          </Col>
        </Row>
        <Row>
          <Col span={6}>
            <div>Tng s SV: 36 </div>
          </Col>
          <Col span={6}>
            <Progress percent={100} status="active" />
          </Col>
          <Col span={24} style={{ textAlign: 'right' }}>
            <div className="btn-group">
              <Button type="primary" className="btnSetDecisionNumber">
                <Icon type="export" /> Lp s hiu bng
              </Button>
              <Button type="primary" className="btnSetDecisionNumber">
                <Icon type="export" /> Lu thng tin VB
              </Button>
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default TopBarInputForTabAwardGraduate;



