import React, { Component } from 'react';
import { Table, Button } from 'antd';
import { setStudentForGraduation } from 'dump/data';
import TopBarInputForTabAwardGraduate from './TopBarInputForTabAwardGraduate';

class TableAwardGraduateDiploma extends Component {
  constructor(props) {
    super(props);
    this.state = {};

    this.columns = [
      {
        title: 'M sinh vin',
        dataIndex: 'code',
        key: 'code',
      },
      {
        title: 'H v tn',
        dataIndex: 'name',
        key: 'name',
      },
      {
        title: 'Chuyn ngnh',
        dataIndex: 'majors',
        key: 'name',
      },
      {
        title: 'Tn lp',
        dataIndex: 'class',
        key: 'class',
      },
      {
        title: 'TBCTN',
        dataIndex: 'tBCTN',
        key: 'tBCTN',
      },
      {
        title: 'Xp loi TN',
        dataIndex: 'rankGraduation',
        key: 'rankGraduation',
      },
      {
        title: 'S hiu bng',
        dataIndex: 'diplomaNumber',
        key: 'diplomaNumber',
      },
      {
        title: 'S Q',
        dataIndex: 'decisionNumber',
        key: 'decisionNumber',
      },
      {
        title: 'Ngy Q',
        dataIndex: 'decisionDate',
        key: 'decisionDate',
      },
      {
        title: 'S vo s',
        dataIndex: 'numberSavedBook',
        key: 'numberSavedBook',
      },
      {
        title: 'Ngy cp',
        dataIndex: 'diplomasDate',
        key: 'diplomasDate',
      },
      {
        title: 'Thao tc',
        dataIndex: 'operation',
        render: () => (
          <Button type="primary" onClick={this.showModal}>
            Xem
          </Button>
        ),
      },
    ];

    this.rowSelection = {
      onChange: (selectedRowKeys, selectedRows) => {
        console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
      },
      getCheckboxProps: record => ({
        disabled: record.name === 'Disabled User', // Column configuration not to be checked
        name: record.name,
      }),
    };
  }

  render() {
    return (
      <div>
        <TopBarInputForTabAwardGraduate />
        <Table
          bordered
          rowSelection={this.rowSelection}
          dataSource={setStudentForGraduation}
          columns={this.columns}
          size="middle"
        />
      </div>
    );
  }
}

export default TableAwardGraduateDiploma;



