import React, { Component } from 'react';
import { Table, Button, Icon } from 'antd';
import { setStudentForGraduation } from 'dump/data';

class TableNotQualifiedToGraduate extends Component {
  constructor(props) {
    super(props);
    this.state = {};

    this.columns = [
      {
        title: 'M sinh vin',
        dataIndex: 'code',
        key: 'code',
      },
      {
        title: 'H v tn',
        dataIndex: 'name',
        key: 'name',
      },
      {
        title: 'Ngy sinh',
        dataIndex: 'dayOfBirth',
        key: 'dayOfBirth',
      },
      {
        title: 'Gii tnh',
        dataIndex: 'sex',
        key: 'sex',
      },
      {
        title: 'Chuyn ngnh',
        dataIndex: 'majors',
        key: 'majors',
      },
      {
        title: 'Tn lp',
        dataIndex: 'class',
        key: 'class',
      },
      {
        title: 'TBCHT TK',
        dataIndex: 'tBCHTTK',
        key: 'tBCHTTK',
      },
      {
        title: 'TBCTN',
        dataIndex: 'tBCTN',
        key: 'tBCTN',
      },
      {
        title: 'Thao tc',
        dataIndex: 'operation',
        render: () => (
          <Button type="primary" onClick={this.showModal}>
            Xem
          </Button>
        ),
      },
    ];

    this.rowSelection = {
      onChange: (selectedRowKeys, selectedRows) => {
        console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
      },
      getCheckboxProps: record => ({
        disabled: record.name === 'Disabled User', // Column configuration not to be checked
        name: record.name,
      }),
    };
  }

  render() {
    return (
      <div>
        <Button type="primary" className="btnSetDecisionNumber">
          <Icon type="export" /> Xt vi tt nghip
        </Button>
        <Table
          bordered
          rowSelection={this.rowSelection}
          dataSource={setStudentForGraduation}
          columns={this.columns}
        />
      </div>
    );
  }
}

export default TableNotQualifiedToGraduate;



