import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Card, Row, Col } from 'antd';
import FormInfoStudentForGraduation from './FormInfoStudentForGraduation';
import MenuTabTop from './MenuTabTop';
import './style.scss';

class ListStudentForTranscriptPage extends Component {
  state = {}
  render() {
    return (
      <Card title="Xt tt nghip ra trng">
        <Row className="rowMargin">
          <Col span={24}>
            <MenuTabTop />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <FormInfoStudentForGraduation />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ListStudentForTranscriptPage />
  </Page>
);



