import React, { Component } from 'react';
import { Row, Col, Menu, Icon } from 'antd';
import './style.scss';

class MenuTabTop extends Component {
  state = {
    current: 'printTranscript',
  };
  handleClick = e => {
    console.log('click ', e);
    this.setState({
      current: e.key,
    });
  };
  render() {
    return (
      <Row>
        <Col span={24}>
          <Menu onClick={this.handleClick} selectedKeys={[this.state.current]} mode="horizontal">
            {/* <Menu.Item key="considerGraduation">
              <Icon type="setting" /> Xt tt nghip
            </Menu.Item> */}
            {/* <Menu.Item key="saveInfoDiploma">
              <Icon type="save" theme="twoTone" twoToneColor="#0066ff" /> Lu thng tin VB
            </Menu.Item> */}
            {/* <Menu.Item key="reviewGraduation">
              <Icon type="right-circle" theme="twoTone" twoToneColor="#00cc00" /> Xt vt tt nghip
            </Menu.Item> */}
            {/* <Menu.Item key="transferNotGraduate">
              <Icon type="left-circle" theme="twoTone" twoToneColor="#00cc00" /> Chuyn cha tt
              nghip
            </Menu.Item> */}
            <Menu.Item key="printGraduationReport">
              <Icon type="printer" theme="twoTone" twoToneColor="#6699ff" /> In bo co tt nghip
            </Menu.Item>
            <Menu.Item key="printStudentReport">
              <Icon type="printer" theme="twoTone" twoToneColor="#6699ff" /> In bo co sinh vin
            </Menu.Item>
            <Menu.Item key="fileExcel">
              <Icon type="file-excel" theme="twoTone" twoToneColor="#00cc00" /> Xut Excel
            </Menu.Item>
          </Menu>
        </Col>
      </Row>
    );
  }
}

export default MenuTabTop;



